                    type: 'Boolean',
                    optional: '—',
                    default: 'true'
                },
                {
                    param: 'history',
                    desc: `如果设置为false禁用历史记录模块（返回按钮以关闭图库，每张幻灯片的唯一URL）。您也可以history.js从构建中排除模块。`,
                    type: 'Boolean',
                    optional: '—',
                    default: 'true'
                },
                {
                    param: 'galleryUID',
                    desc: `图库唯一ID。在形成URL时由History模块使用。`,
                    type: 'Integer',
                    optional: '—',
                    default: '1'
                },
                {
                    param: 'galleryPIDs',
